@import '@alfalab/core-components/vars/index.css';
@import '@alfalab/core-components/themes/corp.css';

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgba(11, 31, 53);
  font-family: system-ui, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu,
  Cantarell, "Open Sans", "Helvetica Neue", "Roboto Rouble", sans-serif;
  background-color: rgb(243, 244, 245);
}
